/* Standard syntax */
@-webkit-keyframes example {
    from {
        background-color: #000;
        border-left-width: 1px;
    }

    to {
        background-color: #222;
        border-left-width: 5px;
    }
}
@keyframes example {
    from {
        background-color: #000;
        border-left-width: 1px;
    }

    to {
        background-color: #222;
        border-left-width: 5px;
    }
}


